import React from "react"
import styled from "styled-components"
import QF from "../components/quickfind"

const search = new QF(100)

var Box = styled.div`
  height: 50px;
  width: 50px;
  background-color: ${props => props.color};
`
const boardConstruct = function(n) {
  var result = []
  for (var i = 0; i < n; i++) {
    var random = Math.floor(Math.random() * Math.floor(10))
    result.push(<Box color={random < 7 ? "white" : "black"} />)
  }
  return result
}

const quicksearch = () => {
  const Section = styled.div`
    display: flex;
    flex-wrap: wrap;
    height: 500px;
    width: 500px;
    @media (max-width: 820px) {
      margin-left: 1em;
      margin-right: 1em;
    }
  `

  return <Section>{boardConstruct(100)}</Section>
}

export default quicksearch
